import { locationChange } from './actions';

export function startListener(history, store) {
  const { pathname, search, hash, key } = history.location;
  store.dispatch(
    locationChange({
      pathname,
      search,
      hash,
      key,
    }),
  );

  history.listen((location, action) => {
    store.dispatch(
      locationChange({
        pathname: location.pathname,
        search: location.search,
        hash: location.hash,
        key: location.key,
        action,
      }),
    );
  });
}
